// GUI stylies of the project
// For example, styles of the links, buttons and so on.


.container{
    width: 1000px;
    margin: 0 auto;
    height: 100%;
    position: relative;
    box-sizing: border-box;
    @include respond-to(tablet){
        width: 100%;
        //padding: 0 20px;
    }
    @include respond-to(mobile){
        width: 100%;
    }

}
.container-inner{
    width: 770px;
    margin: 0 auto;
    @include respond-to(tablet){
        width: 100%;
    }
    @include respond-to(mobile){
        width: 100%;
    }
}

.active_menu{
    position: fixed;
    width: 100%;
}
@media (max-width: 767px) {
    .active_menu{
        position: relative;
    }
}

.active-discount{
    display: block !important;
}
//
.close-discount{
  opacity: 0 !important;
}
//////////////
.main__btn{
    width: 234px;
    .btn {
        text-decoration: none;
        width: 100%;
        min-height: 29px;
        text-align: center;
        font-family: $Effra-medium;
        font-size: 15px;
        //line-height: 29px;
        line-height: 35px;
        position: relative;
        //overflow: hidden;
        color: $black;
        z-index: 2;
        cursor: pointer;
        display: inline-block;
        vertical-align: middle;
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px transparent;
        transition-property: color;
        transition-duration: 0.3s;
        border-radius: 30px;
        &:before{
            content: "";
            position: absolute;
            z-index: -1;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background: $white;
            transform: scaleY(0);
            transform-origin: 50%;
            transition-property: transform;
            transition-duration: 0.3s;
            transition-timing-function: ease-out;
            border-radius: 30px;

        }
    }
    .btn:hover:before, .btn:focus:before, .btn:active:before {
        transform: scaleY(1);
    }
}


//select///
.jq-selectbox__select {
    height: 26px;
    padding-left: 10px;
    width: 176px;
    border: 1px solid black;
    background: white;
    line-height: 32px;
    font-size: 14px;
    font-family: $Regular;
    color: #333;
    margin-top: 20px;
    z-index: 1;
    &:first-child{
        margin-left: 20px;
    }
}
.jq-selectbox__select-text {
    line-height: 24px;
    font-size: 12px;
}

.jq-selectbox li:hover {
    background-color: $yellow;
}
.jq-selectbox__trigger {
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 100%;
    border-left: 1px solid #CCC;
}
.jq-selectbox__trigger-arrow {
    position: absolute;
    top: 11px;
    right: 10px;
    width: 0;
    height: 0;
    border-top: 5px solid #999;
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
    z-index: 1;
}
.jq-selectbox li.selected {
    background-color: $yellow;
}
.jq-selectbox li {
    min-height: 18px;
    padding-left: 10px;
    width: 163px;
    color: #231F20;
    background: $white;
    line-height: 25px;
    font-size: 12px;

}

.jq-selectbox ul{
    max-height: 90px;
    position: relative;
    background: white;
    border: 1px solid black;
    z-index: 2;
}
.label{
    width: 136px;
    font-size: 12px;
    font-family: $Regular;
    display: -webkit-inline-box;
}
.days{
    z-index: 1 !important;
    .jq-selectbox__select {
        width: 128px;
        left: -28px;
        z-index: 1 ;
    }
     ul {
         width: 138px;
         left: -28px;
     }



}
@media (max-width: 370px) {
    .jq-selectbox__select {
        width: 140px;
    }
    .jq-selectbox li {
        width: 129px;
    }
    .days {
        .jq-selectbox__select {
            width: 100px;
        }
        ul {
            width: 108px;
            left: -26px;
        }
    }
}

.price{
    margin: 31px 0 25px 0;
    font-family: $Bold;
    font-size: 15px;
    span{
        position: relative;
        left: 58px;
        top: 5px;
        font-size: 25px;
    }
}
.price-page{
    position: relative;
    top: 17px;
    left: -55px;
    font-style: italic;
    font-size: 10px;
    color: $grey;

}

.button{
    border-radius: 40px;
    width: 328px;
    height: 49px;
    background: $yellow;
    border: 3px solid $yellow;
    transition: $transition;
    font-family: $Effra-medium;
    font-size: 15px;
    cursor: pointer;
    &:hover{
        background: transparent;
        border: 3px solid $yellow;
        transition: $transition;
        color: $yellow;
    }
    &:focus {
        outline: none;
    }
}



/// calc ///


.jq-number {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    padding: 0 36px 0 0;
}

.jq-number__field:hover {
    border-color: #B3B3B3;
}
.jq-number__field input {
    box-sizing: border-box;
    width: 158px;
    position: relative;
    padding: 5px 9px;
    border: 1px solid black;
    left: 103px;
    top: -5px;
    z-index: 1;
    font-size: 14px;
}
.jq-number__field input::-webkit-inner-spin-button,
.jq-number__field input::-webkit-outer-spin-button {
    margin: 0; /* в каких-то браузерах присутствует отступ */
    -webkit-appearance: none;
}
.jq-number__spin {
    position: absolute;
    top: 1px;
    right: 0;
    width: 35px;
    height: 10px;
    background: white;
    cursor: pointer;
}
.jq-number__spin.minus {
    top: -4px;
    left: 262px;
    border-left: 1px solid black;

}
.number-plus-minus .jq-number__spin.plus {
    left: 142px;
    top: -4px;
    border-right: 1px solid black;

}
.jq-number__spin:hover {
    background: white;
}
.jq-number__spin:active {
    background: #F5F5F5;
    box-shadow: inset 1px 1px 3px #DDD;
}
.jq-number__spin:after {
    content: '';
    position: absolute;
    top: 4px;
    left: 11px;
    width: 0;
    height: 0;
    border-right: 5px solid transparent;
    border-bottom: 5px solid #999;
    border-left: 5px solid transparent;
}
.jq-number__spin.minus:after {
    top: 5px;
    border-top: 5px solid #999;
    border-right: 5px solid transparent;
    border-bottom: none;
    border-left: 5px solid transparent;
}

.number-plus-minus .jq-number__spin {
    height: 27px;
    z-index: 2;
}
.words{
    color: $blue;
    text-decoration: none;
    transition: $transition;
    cursor: pointer !important;
    font-style: italic;
    &:hover{
        transition: $transition;
        text-decoration: underline;
        color: $yellow;
    }
}
.help{
    position: relative;
    left: 167px;
    //top: -4px;
    background: #b7e7dc;
    //font-size: 12px;
    padding: 3px 9px;
    border-radius: 100%;
    text-decoration: none;
    cursor: help;
    z-index: 3;
    transition: $transition;
    &:hover::after {
        content: attr(data-title);
        position: absolute;
        left: -178px;
        right: 27px;
        bottom: -21px;
        z-index: 3;
        background: $white;
        color: $black;
        font-family: $Regular;
        font-size: 11px;
        padding: 5px 10px;
        //border: 1px solid #333;
        box-shadow: 0 0 10px 0 rgba(12, 12, 12, 0.2);
        border-radius: 10px;
        transition: $transition;

    }
}

@media (max-width: 370px) {
    .jq-number__field input {
        width: 122px

    }
    .jq-number__spin.minus {
        left: 226px;
    }
    .help {
        position: relative;
        left: 130px;
    }
    .button{
        width: 310px;
    }

}
///scroll//

::-webkit-scrollbar-track {
    background-color: white;
    box-shadow:0px 0px 3px #000 inset;
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color:#a1d3f9;

}
::-webkit-scrollbar{
    width: 13px;
}


/////////main-slider////////////////
.bx-wrapper .bx-viewport {
    transform: translatez(0);
    //height: 100% !important;
}

.bx-wrapper .bx-pager,
.bx-wrapper .bx-controls-auto {
    position: absolute;
    bottom: -15%;
    width: 100%;
    //height: 100%;
}

/* PAGER */

.bx-wrapper .bx-pager {
    text-align: center;
    font-size: .85em;
    font-weight: bold;
    padding-top: 20px;

}

.bx-wrapper .bx-pager .bx-pager-item,
.bx-wrapper .bx-controls-auto .bx-controls-auto-item {
    display: inline-block;
    *zoom: 1;
    *display: inline;

}

.bx-wrapper .bx-pager.bx-default-pager a {
    background: #666;
    text-indent: -9999px;
    display: block;
    width: 8px;
    height: 8px;
    margin: 0 7px;
    outline: 0;
    border-radius: 5px;
    background: $black;
    opacity: 0.2;
    position: relative;
}

.bx-wrapper .bx-pager.bx-default-pager a:hover,
.bx-wrapper .bx-pager.bx-default-pager a.active {
    background: $blue;
    opacity: 0.5;
    position: relative;
    display: block;
}


/* DIRECTION CONTROLS (NEXT / PREV) */

.bx-wrapper .bx-prev {
    left: -55px;
    background: url(../img/content/direction_controlls.png)no-repeat;
    transform: rotate(180deg);
}

.bx-wrapper .bx-next {
    right: -45px;
    background: url(../img/content/direction_controlls.png)no-repeat;
}


.bx-wrapper .bx-controls-direction a {
    position: absolute;
    top: 65%;
    outline: 0;
    width: 40px;
    text-indent: -50px;
    z-index: 9999;
    color: transparent;
}

.bx-wrapper .bx-controls-direction a.disabled {
    display: none;
}
