// File for scss-vars.
// For example: main colors, paddings and so on.

//COLORS
$blue: #2eb3ff;
$green: #23a811;
$yellow: #ffc20f;
$darkyellow: #f5b809;
$white: #fff;
$black: #000;
$lightblue: #d7e9ef;
$grey-orange: #6c6962;
$grey: #464646;


@font-face{
    font-family: Montserrat-Black;
    src: local("Montserrat-Black"), url(../fonts/Montserrat-Black.otf)
}

@font-face{
    font-family: Montserrat-Bold;
    src: local("Montserrat-Bold"), url(../fonts/Montserrat-Bold.otf)
}
@font-face{
    font-family: Montserrat-Regular;
    src: local("Montserrat-Regular"), url(../fonts/Montserrat-Regular.otf)
}
@font-face{
    font-family: Effra;
    src: local("Effra"), url(../fonts/Dalton-Maag-Effra.ttf)
}
@font-face{
    font-family: Effra-medium;
    src: local("Effra-medium"), url(../fonts/Dalton-Maag-Effra-Medium.ttf)
}
@font-face{
    font-family: Effra-Italic;
    src: local("Effra-Italic"), url(../fonts/Dalton-Maag-Effra-Italic.ttf)
}


$Black: Montserrat-Black, sans-serif;
$Bold: Montserrat-Bold, sans-serif;
$Regular: Montserrat-Regular, sans-serif;
$Effra: Effra, sans-serif;
$Effra-medium: Effra-medium, sans-serif;
$Effra-Italic: Effra-Italic, sans-serif;



$transition: all linear 0.5s;
