// Common styles of the project







body{
    opacity: 0;
}

.clearfix {
    overflow: auto;
    zoom: 1;
}
h1{
    font-size: 30px;
    font-family: $Regular;
    color: $black;
    text-transform: uppercase;
    position:relative
}
h2{
     font-size: 30px;
     font-family: $Bold;
     color: $black;
     text-transform: uppercase;
 }

h4{
    font-size: 16px;
    font-family: $Regular;
    color: $black;
}
h5{
    font-size: 14px;
    font-family: $Effra-Italic;
    color: $black;
}

@media (max-width: 1099px) {
    h1{
        font-size: 25px;
    }
    h2{
        //font-size: 25px;
    }
}
@media (max-width: 767px) {
    h1{
        font-size: 20px;
    }
    h2{
        font-size: 25px;
    }
}


/*
  ====================================================================
  Header
  ====================================================================
*/

.main-header{
    //opacity: 0;
    background: $white;
    min-height: 90px;
    //position: relative;
    z-index: 2;
    &__buttons{
        .main__btn{
            float: right;
            padding-top: 7px;
        }
        .first {
            width: 112px;
            margin-right: 15px;
            float: right;
            background-color: #fdfdfd;
            border: 1px solid #f8f7f7;
            &:before {
                //background: $darkyellow;
            }
        }
        .first:hover, .first:focus, .first:active {
            color: $yellow;
            border: 1px solid $yellow;
            background: $darkyellow;
        }
        .second {
            width: 99%;
            background-color: $darkyellow;
            border: 1px solid $darkyellow;
        }
        .second:hover, .second:focus, .second:active {
            color: $darkyellow;
            border: 1px solid $darkyellow;
        }
    }
    &__menu{
        margin-top: 5px;
        padding-top: 5px;
        border-top: 2px solid #f2ede4;
    }
    &__nav{
        margin: 0;
        padding-top: 7px;
        float: left;
        padding-left: 0;
        width: 650px;
        z-index: 9;
        ul{
        }
        a{
            text-decoration: none;
            font-family: $Effra-medium;
            color: $black;
            font-size: 14px;
            //padding: 10px 0;
            border-bottom: 3px solid transparent;
            &:hover{
                transition: $transition;
                border-bottom: 3px solid $yellow;
                padding-bottom: 5px;
                color: $darkyellow;
            }
        }
        li{
            float: left;
            list-style: none;
            margin: 0 11px;
            transition: $transition;
            ul{
                display: none;
                padding: 0;

            }
            li{
                width: 180px;
                height: 30px;
                float: none;
                margin: 0;
                padding-left: 8px;
                line-height: 30px;
                transition: $transition;
                &:hover{
                    transition: $transition;
                    background: $yellow;
                }
                &:first-child{
                    margin: 0;
                }
                a{
                    &:hover{
                        color: $black;
                    }
                }
            }
            &:hover ul{
                display: block;
                position: absolute;
                z-index: 20;
                top: 90px;
                height: 150px;
                padding: 0;
                left: 0;
                background: $white;
                transition: $transition;
            }
        }

        .icon{
            height: 15px;
            background-size: cover;
            background: url(../img/content/icon.png) no-repeat;
            width: 10px;
            position: relative;
        }
        .service{
            left: 57px;
            top: -12px;
        }
        .more{
            left: 35px;
            top: -25px;
        }
        .bells{
            height: 15px;
            background-size: cover;
            background: url(../img/content/bells_des.png) no-repeat;
            width: 10px;
            position: relative;
            left: 30px;
            top: -23px;
        }
        .discounts{
            left: 60px;
            top: -23px;
        }
    }
    &__contacts{
        float: left;
        li{
            float: left;
            list-style: none;
            transition: $transition;
        }
        .main__btn{
            width: 164px;
            margin-top: -6px;
            margin-left: -36px;
        }
        .btn {
            width: 100px;
            background-color: #f0fbef;
            border: 1px solid $green;
            color: $green;
            position: relative;
            padding-right: 27px;
            left: 20px;
            line-height: 29px;

        }
        .btn:hover, .btn:focus, .btn:active {
            color: $green;
            border: 1px solid $green;
        }
        .circle{
            height: 12px;
            width: 12px;
            border-radius: 100%;
            background-size: cover;
            background: #3ced24;
            position: absolute;
            top: 9px;
            left: 105px;
        }

  //////////////////
        li {
            font-family: $Effra-medium;
            padding: 8px 5px 0 5px;
            &:last-child{
                position: relative;
                left: 30px;
            }
            ul {
                display: none;
                padding: 0;
            }
            a {
                text-decoration: none;
                font-family: $Effra-medium;
                color: $black;
            }
            li {
                width: 40px;
                float: none;
                margin: 0;
                padding-left: 8px;
                line-height: 30px;
                transition: $transition;
                &:last-child{
                    position: static;
                }
                &:hover {
                    transition: $transition;
                    background: $yellow;
                }
                a {
                    text-decoration: none;
                    &:hover {
                        color: $black;
                    }
                }
            }
            &:hover ul {
                display: block;
                position: absolute;
                top: 35px;
                padding: 0;
                z-index: 2;
                background: $white;
            }
        }
        .icon{
            height: 15px;
            background-size: cover;
            background: url(../img/content/icon.png) no-repeat;
            position: relative;
            left: 37px;
            top: -10px
        }
        .phone{
            position: relative;
            &:after{
                content: '';
                position: absolute;
                height: 17px;
                width: 1px;
                background: $yellow;
                right: -13px;
            }
        }
    }
}

#touch-menu {
    display: none;
}




@media (max-width: 1099px) {
    .main-header{
        min-height: 50px;
        &__nav {
            display: none;
            padding-top: 0px;
            float: right;
            padding-left: 0;
            width: 280px;
            min-height: 230px;
            background: $yellow;
            position: absolute;
            right: 1px;
            li {
                float: none;
                padding: 9px 0 0 25px;
                color: $black;

                &:nth-child(2){
                    margin-bottom: -15px;
                }
                &:nth-child(6){
                    margin-bottom: -15px;
                }

                ul {
                    display: none;
                    margin-top: -20px;
                    height: 170px;
                }
                li{
                    height: 25px;
                    padding-left: 25px;
                    &:nth-child(2){
                        margin-bottom: 0px;
                    }
                    &:nth-child(6){
                        margin-bottom: 0px;
                    }
                }
                &:hover{
                    background: $yellow;
                    color: $black;
                }
                &:hover ul{
                    display: block;
                    position: static;
                    height: 180px;
                    background: $yellow;
                    color: $black;

                }
            }
            .icon {
                background: url(../img/content/icon_mob.png) no-repeat;
            }
            a:hover{
                color: $black;

            }
        }
        .bells{
            height: 15px;
            background-size: cover;
            background: url(../img/content/bells_mob.png) no-repeat;
            width: 16px;
            position: relative;
            left: -22px;
            top: -13px;
        }
        .discounts{
            left: -22px;
            top: -13px;
        }
        .more{
            margin-bottom: -34px;
        }
        &__menu{
            border: none;
        }
        &__buttons .main__btn {
            padding-right: 15px;
        }
        &__contacts {
            margin: -44px 0 0 20px;
            li:last-child {
                position: relative;
                left: 0px;
            }
            .phone {
                &:after {
                    right: 0px;
                }
            }
        }
    }
    #touch-menu {
        display: block;
        width: 30px;
        height: 21px;
        background-size: cover;
        background: url(../img/content/menu_burger.png) no-repeat;
        float: right;
        z-index: 1;
        position: relative;
        top: 15px;
        right: 10px;
        transition: $transition;

    }
    #close-menu {
        display: block;
        width: 50px;
        height: 55px;
        background-size: cover;
        background: url(../img/content/menu_close.png) no-repeat;
        top: 2px;
        right: 0px;
        position: absolute;
        float: right;
        z-index: 2;
        transition: $transition;
        &:focus{
            transition: $transition;
            opacity: 0;
        }
    }
}



@media (max-width: 767px) {
    .main-header {
        min-height: 70px;
        &__contacts{
            .main__btn{
                display: none;
            }
        }
    }
}

@media (max-width: 650px) {
    .main-header {
        min-height: 150px;
        &__nav{
            position: absolute;
            right: 0;
            z-index: 5;
        }
        &__buttons{
            padding-top: 50px;
            float: right;
            .main__btn{
                padding-top: 7px;
                padding-right: 0;
                float: left;
                width: 137px;

            }
            .second{
                width: 150px;
                height: 50px;
                line-height:50px;
            }
            .first{
                margin-right: 0;
                height: 50px;
                line-height:50px;
            }
        }
        &__contacts {
            float: right;
            margin: 0;
            padding-right: 50px;
        }
        #touch-menu {
            right: 20px;
        }
        #close-menu {
            right: 15px;
        }
    }
}

