form{
    .select{
        padding: 0 20px;
    }
    .currency__header{
        position: relative;
        top: -10px;
        left: 10px;
    }
    .radio_btn{
        margin: 16px 0 14px 22px;
    }
    .currency{
        z-index: 2;
        position: relative;
        top: 4px;
    }
    .radio {
        z-index: 1;
        position: relative;
    }
    .radio + label {
        cursor: pointer;
    }
    .radio:not(checked) {
        position: absolute;
        opacity: 0;
    }
    .radio:not(checked) + label {
        position: relative;
        padding: 0 13px 0 14px;
    }
    .radio:not(checked) + label:before {
        content: '';
        position: absolute;
        top: -1px;
        left: 0;
        width: 63px;
        height: 28px;
        border: 1px solid $black;
        background: #FFF;
    }
    .radio:focus + label:before {
        background: #e3f5fe;
        //opacity: 0.5;
        width: 80px;

    }
}

.total{
    width: 81px;
    border: none;
    padding-left: 7px;
}

@media (max-width: 370px) {
    form {
        .select {
            padding: 0 0 0 10px;
        }
        .radio:not(checked) + label {
            position: relative;
            padding: 0 9px;
        }
    }
}


