@media (max-width: 1099px) {
    .discount-open {
        &__text:before, span:before {
            display: none;
        }
    }
}

@media (max-width: 768px) {
    .discount-open {
        &__close {
            right: 8px;
        }



    }
}
@media (max-width: 767px) {
    .discount-open {
        &__text:before, span:before {
            display: none;
        }
        h3 {
            width: 95%;
            //font-size: 22px;
        }
    }
}
@media (max-width: 320px) {
    .discount-open {
        &__text:before, span:before {
            right: 120px;
            top: 120px;
            display: block;
        }

        span{
            font-size: 20px;
        }
    }
}



/*
  ====================================================================
  Footer
  ====================================================================
*/

.main-footer{
    min-height: 400px;
    background: $black;
    &__list{
        padding-top: 30px;
        border-bottom: 1px solid $grey-orange;
        min-height: 260px;

    }
    &__left{
        float: left;
        width: 710px;
    }
    a{
        text-decoration: none;
        font-size: 14px;
        font-family: $Regular;

    }
    &__above{
        height: 50px;
        li{
            float: left;
            width: 18%;
            &:last-child{
                width: 26%;
            }
        }
        a{
            color: $white;
            border-bottom: 3px solid transparent;
            &:hover{
                transition: $transition;
                border-bottom: 3px solid $yellow;
                padding-bottom: 5px;
                color: $darkyellow;
            }
        }
    }
    &__middle{
        height: 110px;
        border-bottom: 1px solid $grey-orange;
        border-top: 1px solid $grey-orange;
        li{
            float: left;
            width: 25%;
            padding-top: 40px;
        }
        a{
            color: $grey;
            line-height: 25px;
            &:hover{
                transition: $transition;
                color: $white;
            }
        }

    }
    &__below{
        height: 110px;
        li{
            float: left;
            width: 23%;
            //padding-top: 30px;
            padding-bottom: 10px;
            padding-left: 29.5px;
            color: $grey-orange;
            line-height: 20px;
            font-family: $Regular;
            font-size: 12px;
            background: url("../img/content/contakts.png") no-repeat;
            transition: $transition;
            filter: grayscale(100%);
            cursor: pointer;
            margin: 30px 0;
            &:first-child{
                width: 37%;
            }

            &:hover {
                transition: $transition;
                filter: grayscale(0);
            }

        }
        a{
            color: $grey-orange;
            line-height: 20px;
        }

        .address{
            background-position: 0 0px;
        }
        .phone{
            background-position: 0 -52px;
        }
        .mail{
            background-position: 0 -97px;
        }
    }
    &__right{
        float: left;
        width: 250px;
        padding-left: 40px;
        height: 300px;
        &:before {
            content: '';
            position: absolute;
            top: 85px;
            right: 265px;
            width: 1px;
            height: 180px;
            background-color: $grey-orange;
            z-index: 2;
        }
        li{
            float: left;
            width: 50%;
            font-size: 14px;
            color: $white;
            font-family: $Regular;
            text-align: center;
            line-height: 27px;

        }
    }
    .cards{
        background: url("../img/content/cards.png") no-repeat;
        transition: $transition;
        filter: grayscale(0);
        cursor: pointer;
        padding: 19px 0px;
        margin: 5px 0;
        &:hover {
            transition: $transition;
            filter: grayscale(100%);
            transform: scale(0.98);
        }
    }
    .maestro{
        background-position: 0 0;
    }
    .mastercard{
        background-position: -60px -1px;
    }
    .visa{
        background-position: 0 -48px;
    }
    .paypal{
        background-position: -60px -48px;
    }
    .american{
        background-position: 0 -96px;
    }
    .discover{
        background-position: -60px -96px;
    }
    .norton{
        background: url("../img/content/norton.png") no-repeat;
        transition: $transition;
        filter: grayscale(0);
        cursor: pointer;
        padding: 29px 16px;
        margin: 5px 28px;
        &:hover {
            transition: $transition;
            filter: grayscale(100%);
            transform: scale(0.98);
        }
    }
    p{
        font-size: 13px;
        color: $grey-orange;
        text-align: center;
        position: relative;
        top: 103px;
        left: -70px;
    }
    &__social{
        margin: 0 auto;
        width: 200px;
    }
    .social{
        background: url("../img/content/social.png") no-repeat;
        transition: $transition;
        filter: grayscale(0);
        cursor: pointer;
        padding: 19px 0px;
        margin: 0 10px;
        width: 45px;
        float: left;
        &:hover {
            transition: $transition;
            filter: grayscale(100%);
            transform: scale(0.98);
        }
    }
    .facebook{
        background-position: 0 0;
    }
    .twitter{
        background-position: -66px 0;
    }
    .google{
        background-position: -132px 0;
    }
}
@media (max-width: 1099px) {
    .main-footer{
        min-height: 670px;
        &__list{
            width: 710px;
            margin: 0 auto;
        }
        &__left {
            float: none;
            margin: 0 auto;
        }
        &__right {
            float: none;
            margin: 0 auto;
            height: 200px;
            padding-top: 20px;
            padding-left: 0;

            &:before{
                display: none;
            }
        }
        &__below{
            border-bottom: 1px solid $grey-orange;

        }
        &__social{
            padding-top: 20px;
            margin: 0 auto;
        }
        p{
            left: -67px;
            top: 190px;

        }
    }
}

@media (max-width: 709px) {
    .main-footer{
        min-height: 780px;
        &__list{
            width: 100%;
        }
        &__left {
            width: 98%;
            li{
                width: 33%;
                line-height: 23px;
                &:nth-child(4){
                    width: 40%;
                }
                &:last-child{
                    width: 60%;
                }
            }
        }
        &__above{
            height: 80px;
        }
        &__middle{
            height: 100%;
            padding: 20px;
            li{
                width: 50%;
                padding: 0;
                margin: 0 auto;
                &:last-child{
                    width: 50%;
                }
            }
        }
        &__below{
            padding-left: 10px;
            height: 170px;
            li{
                width: 80%;
                padding-bottom:0;
                margin: 10px 0;
                &:first-child{
                    width: 80%;

                }
            }
        }
    }
}
